var PromiseLib   = Promise;
var utilsDate    = require('./date');


/**
 * @desc: æ¨¡æsleep.
 * @return: Promise.
 *     å¨msæ¶é´åæ§è¡.
 * @e.g.
 *     febs.utils.sleep(1000).then(()=>{
          //1000msä¹åresolve.
       });
 */
exports.sleep = function(ms) {
  return new PromiseLib(function (resolve, reject) {
    try {
      setTimeout(function(){
        resolve();
      }, ms);
    } catch(err) {
      reject(err);
    }
  });
}

/**
 * @desc: åå¹¶å¤ä¸ªmap (æµæ·è´).
 * @return: {}
 */
exports.mergeMap = function()
{
  var map0 = {};
  var map2;
  for (var i=0;i<arguments.length;i++){
    map2=arguments[i];
    if (map2)
    {
      for (var k in map2){
        map0[k]=map2[k]
      }
    }
  }

  return map0;
};
/**
* @desc: å¤æ­åæ°æ¯å¦æ¯null,undefined,NaN
* @return: boolean
*/
exports.isNull = function(e) {
  return e === null || e === undefined || Number.isNaN(e);
}

